import {mapState,mapMutations} from 'vuex';
export default {
	components: {
		
	},
	data() {
		return {
			account:'',
			trueName:'',
			sex:'',
			regTime:'',
			mobile:'',
			idcard:'',
			addressPreffix:'',
			isDefault:false,
			cityPickerValueDefault: [0, 0, 1],
			themeColor: '#007AFF',
			bottomBtnText:'新增',
			addressCodeArr:[]
			
		}
	},
	computed: mapState(['token']),
	onShow(){
		this.getUserInfo();
	},
	onLoad() {
		
	},
	methods: {
		getUserInfo(){ //获取用户信息
			uni.request({
				url: this.websiteUrl + 'user/info',
				method: 'POST',
				data:{},
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':this.token},
				success: res => {
					var res = res.data;
					if(res.code == 200){
						console.log("获取用户信息成功："+JSON.stringify(res.data));
						let data = res.data;
						let account = data.userName;
						let trueName = data.trueName;
						let sex = data.sex == 1?'男':'女';
						var date = new Date(data.regTime * 1000);
						var m = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '月';
						var d = (date.getDate() < 10) ? '0'+date.getDate()+ ' ': date.getDate() + '日';
						let regTime = m + d;
						let idcard = data.idcard;
						let mobile = data.mobile;
						this.account = account;
						this.trueName = trueName;
						this.sex = sex;
						this.regTime = regTime;
						this.idcard = idcard;
						this.mobile = mobile;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		modifyUserInfo(){
			uni.navigateTo({
				url:'/pages/mycenter/user-info/user-info-edit/user-info-edit'
			})
		}
	}
}
